import React from 'react'
import Layout from '../components/layout'

import jasperImg from '../images/jasper.png'
import gesaImg from '../images/gesa.jpg'

const ImgSquare = ({ src }) => (
  <div style={{ background: src, width: 250, height: 250 }} />
)

const IndexPage = () => (
  <Layout>
    <section
      style={{
        paddingTop: 30,
        width: '60%',
        // marginLeft: "20%",
        display: 'flex',
        // alignItems: 'center',
        // justifyContent: 'space-evenly',
      }}
    >
      {/* <ImgSquare src={jasperImg} /> */}
      <section>
        <img
          style={{ borderRadius: 8, marginRight: 48 }}
          src={jasperImg}
          width={150}
          alt="Jasper"
        />
        <caption>Jasper</caption>
      </section>
      <section>
        <img
          style={{ borderRadius: 8, filter: 'saturate(0)', marginRight: 48 }}
          src={gesaImg}
          width={150}
          alt="Gesa"
        />
        <caption>Gesa</caption>
      </section>
    </section>
    <h1 style={{ marginTop: 30 }}>Hey, this is kitchen.</h1>
    <p>
      We gather recipes for our own convenience here. We tend to forget recipes
      that we haven't cooked in a while, but really made us happy eating, no
      matter if we they were our own creation or from someone else.
    </p>
    <p>
      To always be able to find the stuff we like, we created this little
      kitchen -- a collection of own creations, variations (or simply copies) of
      other recipes (we give them credit here).
    </p>
    <p>We hope you can find something you like, too. If so, leave some love.</p>
    <p>With lots of love, Jasper and Gesa</p>
  </Layout>
)

export default IndexPage
